import React, { Component } from 'react';
import '../../style/gearbox/srg.css';
import Dropdown from 'semantic-ui-react';
import {gearProcesses} from '../common/solverOptions';
import { withStyles, makeStyles } from "@material-ui/core/styles";
import Slider from "@material-ui/core/Slider";
import Tooltip from "@material-ui/core/Tooltip";
import { Link } from 'react-router-dom';
import Joi from 'joi-browser';
import Input from '../common/input';
import Form from '../common/form';

const PrettoSlider = withStyles({
  root: {
    color: "#52af77",
    height: 8
  },
  thumb: {
    height: 24,
    width: 24,
    backgroundColor: "#52af77",
    border: "2px solid currentColor",
    marginTop: -8,
    marginLeft: -12,
    "&:focus, &:hover, &$active": {
      boxShadow: "inherit"
    }
  },
  active: {},
  valueLabel: {
    left: "calc(-50% + 4px)"
  },
  track: {
    height: 8,
    borderRadius: 4
  },
  rail: {
    height: 8,
    borderRadius: 4
  }
})(Slider);


export default class Srg extends Form {

    state = {
        data: {
            faceWidth1:"",
            faceWidth2:"",
            overloadFactor:"",
            qualityNumber:"",
            gearboxLength:"",
            rpm:"",
            torque:"",
            power:"",
            cvt:"",
            hardness:"",
            elasticModulus:""
        },

        errors:{},
        processes : gearProcesses()
    }
    schema = {
        faceWidth1: Joi.number().label('Face Width').required(),
        faceWidth2: Joi.number().label('Face Width').required(),
        overloadFactor: Joi.label('Overload Factor'),
        qualityNumber: Joi.label('Quality Number'),
        gearboxLength: Joi.label('Gearbox Length'),
        rpm : Joi.number().label('Motor RPM').required(),
        torque: Joi.number().label('Motor torque').required(),
        power : Joi.number().label('Motor Power').required(),
        cvt : Joi.label('CVT Ratio'),
        hardness : Joi.number().label('Material Hardness').required(),
        elasticModulus : Joi.number().label('Elastic Modulus').required()

    }
    doSubmit() {
        console.log(this.state.data);
    }
    render() {
        const { processes } = this.state;
        return (
            <React.Fragment>
              <form onSubmit={this.handleSubmit}>

                <div className="row m-0">

                    <div className="col-md-6 l">
                        <div id="firstRow" className="row background-2 border m-5 justify-content-around first-row">
                            <div className="col-md-12 border-bottom border-warning mb-4 mt-2 p-3 is-white text-left" style={{fontSize:"35px", fontWeight:"600", fontFamily:"Fira Code"}}>
                                <span>Loading</span>
                            </div>
                            {this.renderInput('rpm', 'Motor/Engine RPM')}
                            {this.renderInput('torque', 'Motor/Engine torque')}
                            {this.renderInput('power', 'Motor/Engine power')}
                            <div className="col-md-12 is-white"><h2 className=" text-left m-5 is-firaCode pageInfo">If a CVT is used for primary reduction then CVT ratio is required</h2></div>
                            {this.renderInput('cvt','Max CVT Ratio')}</div>
                            <div id="firstRow" className="row background-2 border m-5 justify-content-around first-row">
                            <div className="col-md-12 mb-4 border-bottom border-warning mt-2 p-3 is-white text-left" style={{fontSize:"35px", fontWeight:"600", fontFamily:"Fira Code"}}>
                                <span>Material Data</span>
                            </div>
                            {this.renderInput('hardness', 'Material Hardness')}
                            {this.renderInput('elasticModulus', 'Elastic Modulus')}
                            <div className="col-md-12 is-white"><h2 className="text-left m-5 is-firaCode">According to AGMA specification Allowable bending and contact stress depends on manufacturing process. Some of the common gear processes are listed below.</h2>
                            </div>
                            <div className="form-row m-0">
                                <select className="m-4" id="content">
                                    {processes.map(p => <option style={{fontWeight:"600", fontFamily:"Fira Code"}} key={p.id} value={p.value}>{p.label}</option>)}
                                </select>
                            </div>
                            <div className="form-row m-0 is-white">
                            <div className="col-md-12">Steel Grade</div>
                            <div class="form-check form-check-inline m-4">
                              <input defaultChecked class="form-check-input" type="radio" name="inlineRadioOptions" id="inlineRadio1" value="option1" />
                              <label class="form-check-label" for="inlineRadio1">1</label>
                                </div>
                                <div class="form-check form-check-inline m-0">
                              <input class="form-check-input" type="radio" name="inlineRadioOptions" id="inlineRadio2" value="option2" />
                              <label class="form-check-label" for="inlineRadio2">2</label>
                            </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-md-6 r">
                        <div id="firstRow" className="row background-2 border m-5 justify-content-around first-row">
                            <div className="col-md-12 border-bottom border-warning mb-4 mt-2 p-3 is-white text-left" style={{fontSize:"35px", fontWeight:"600", fontFamily:"Fira Code"}}>
                                <span>Gear Data</span>
                            </div>
                            <div className="form-row m-0">
                                <div className="form-group ml-2">
                                    <div style={{width:"290px"}}>
                                      <label className="is-white">Preferred Module(1)</label>
                                      <PrettoSlider
                                        valueLabelDisplay="auto"
                                        aria-label="pretto slider"
                                        defaultValue={2}
                                        step={0.25}
                                        min={0.25}max={4.5}
                                        onChange={this.handleChange}
                                      />
                                    </div>
                                </div>
                            </div>
                            <div className="form-row m-0">
                                <div className="form-group ml-2">
                                    <div style={{width:"290px"}}>
                                      <label className="is-white">Preferred Module(2)</label>
                                      <PrettoSlider
                                        valueLabelDisplay="auto"
                                        aria-label="pretto slider"
                                        defaultValue={2}
                                        step={0.25}
                                        min={0.25}max={4.5}
                                      />
                                    </div>
                                </div>
                            </div>
                                {this.renderInput('faceWidth1','Preferred Face Width(1)')}
                                {this.renderInput('faceWidth2','Preferred Face Width(2)')}
                        </div>
                        <div id="firstRow" className="row background-2 border m-5 justify-content-around first-row">
                            <div className="col-md-12 border-bottom border-warning mb-4 mt-2 p-3 is-white text-left" style={{fontSize:"35px", fontWeight:"600", fontFamily:"Fira Code"}}>
                                <span>Constraints</span>
                            </div>
                                {this.renderInput('overloadFactor','Overload Factor')}
                                {this.renderInput('qualityNumber','quality Number')}
                            <div className="col-md-12 is-white">
                                <h3 className=" text-left m-5 is-firaCode pageInfo">
                                    Value of overload factor will be decided on the basis of shocks, that are coming on Gears, while in motion<br/><br />Quality Number is manufacturer's specs. and can be asked from the manufacturer, or refer the
                                    <span><Link to="/docs"><u style={{fontWeight:"400", fontFamily:"Fira Code", color:"#fff"}}>documentation</u></Link></span> if not known
                                </h3>
                            </div>
                                {this.renderInput('gearboxLength','Max Length of Gearbox')}
                            <div className="col-md-12 is-white">
                                <h3 className=" text-left m-5 is-firaCode pageInfo">
                                    If value of overload factor is not provided then default value will be 1.25<br/><br />
                                    If value of Ouality Number is not provided then default value will be 11.
                                </h3>
                            </div>
                        </div>
                        <div className="row m-0 mb-5">
                            <div className=" m-0 col-md-12 d-flex justify-content-center">
                                {this.renderButton("Solve")};
                            </div>
                        </div>
                    </div>
                </div>
            </form>

            <div className="jumbotron jumbotron-fluid m-0 bg-dark is-white">
                <Link to='/'><h1 className="text-center is-firaCode">If you have your gear configuration and want it to be tested</h1></Link>
            </div>
            </React.Fragment>
        );
    }
}